import type { EventEmitter } from '@stencil/core';
import { Component, Host, h, Prop, Event, Element } from '@stencil/core';
import { getColors } from '../../internal/color';

@Component({
	tag: 'o-tag',
	styleUrls: ['o-tag.scss'],
	shadow: true
})
export class OTag {
	@Element() host!: HTMLElement;

	/** Determines if the tag is closable (shows a close button) */
	@Prop() closable: boolean = false;

	/** Color variant of the tag */
	@Prop() color: 'brand' | 'order' | 'payment' | 'shipping' | 'uncategorized' = 'uncategorized';

	/** Event emitted when the close button is clicked */
	@Event() oRemove: EventEmitter<void>;

	/** Event emitted when the tag is clicked */
	@Event() oClick: EventEmitter<void>;

	private handleRemoveClick(event: MouseEvent) {
		event.stopPropagation(); // Prevent triggering the click event on the tag
		this.oRemove.emit();
		(this.host as HTMLElement).remove(); // Remove the o-tag from the DOM
	}

	/** Handles click events on the tag */
	private handleClick = (event: MouseEvent) => {
		if (this.oClick) {
			event.preventDefault();
			event.stopPropagation();
			return;
		}
		event.preventDefault();
		this.oClick.emit();
	};

	render() {
		const baseClasses =
			'inline-flex items-center justify-center font-inter font-medium text-sm rounded-full px-4 py-1 max-w-full truncate';

		const categoricalColors = getColors('categorical');

		return (
			<Host>
				<div
					class={`${baseClasses} relative`}
					onClick={this.handleClick}
					style={{
						backgroundColor: `${categoricalColors[this.color].textColor}`,
						border: `1px solid ${categoricalColors[this.color].color}`,
						color: `${categoricalColors[this.color].color}`
					}}
				>
					<div class={`flex items-center gap-2 ${this.closable ? 'mr-4' : ''} max-w-full`}>
						<slot name="startIcon"></slot>
						<span class="truncate">
							<slot></slot>
						</span>
						{this.closable && (
							<o-icon-button
								name="close"
								color={categoricalColors[this.color].color}
								class="absolute right-1"
								onClick={(event) => this.handleRemoveClick(event)}
							></o-icon-button>
						)}
					</div>
				</div>
			</Host>
		);
	}
}
